<template>
  <div class="home">
    <v-container>
      <v-card
        elevation="0"
        class="mx-auto white mt-4"
        max-width="350"
        flat
        max-height="250px"
        style="border-radius: 25px"
      >
        <v-row class="pt-3 pr-5" align="center" justify="center">
          <v-col class="ml-1" cols="3" align="center">
            <v-img
              src="../assets/img/icon_Bluetooth.png"
              max-height="130px"
              max-width="70px"
              contain
            ></v-img>
          </v-col>

          <v-col class="pl-0" cols="8">
            <div class="ml-3 overline">
              Devicename :
              <!-- <v-icon
                class="pl-8"
                :color="this.status == 'connect' ? 'grey' : 'green'"
                style="margin-left: 22px"
                small
                >mdi-circle</v-icon -->
              <!-- > -->
            </div>

            <v-card-title
              class="truncatecus pa-0 ml-3 black--text font-weight-black"
              >{{ array_device[0].name }}</v-card-title
            >
            <v-card-title
              class="truncatecus pa-0 ml-3 grey--text font-weight-black body-2"
              >{{ Deviceid + array_device[0].id }}</v-card-title
            >
          </v-col>
        </v-row>
        <v-divider class="mx-10 mt-5" inset></v-divider>
        <v-row class="mt-1 pb-2 pl-0" align="right" justify="center">
          <v-col class="ml-6 mt-1" align="left">
            <span class="font-weight-black overline"
              >Status :
              <v-icon
                class="ml-2"
                :color="this.status == 'connect' ? 'grey' : 'blue'"
              >
                {{ this.status_icon }}
              </v-icon>
            </span>
          </v-col>

          <v-col class="pl-0 mr-7" align="right">
            <v-btn
              class="white--text mr-2"
              style="width: 155px; height: 40px; border-radius: 13px"
              :loading="loading"
              @click="Status_connect_dis_Device()"
              :color="this.status == 'connect' ? 'green' : 'red'"
            >
              {{ this.status }}
              <v-icon class="ml-2">{{ this.status_icon_btn }}</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-card>

      <v-card
        v-if="this.status == 'disconnect'"
        elevation="0"
        class="mx-auto white mt-7"
        max-width="350"
        flat
        style="border-radius: 25px"
      >
        <v-row class="" align="center" justify="center">
          <v-col class="ml-6 mt-3" align="left">
            <span class="font-weight-black overline"
              >Click button below to UNLOCK :
              <v-icon
                class="ml-2"
                :color="this.status_lock == 'lock' ? 'red' : 'green'"
              >
                {{ this.statuslock_icon }}
              </v-icon>
            </span>
          </v-col>
        </v-row>
        <v-row class="pt-2" align="center" justify="center">
          <v-col align="center">
            <v-btn
              class="mx-2"
              @click="connectDeviceAndCacheCharacteristics()"
              fab
              dark
              :style="
                this.status_lock == 'lock'
                  ? 'height: 150px; width: 150px; background: linear-gradient(to right, #e52d27, #b31217);'
                  : 'height: 150px; width: 150px;  background: linear-gradient(to right, #56ab2f, #9dd955);'
              "
            >
              <v-icon dark size="90px" color="white">{{
                this.statuslock_icon
              }}</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-divider class="ma-2 mt-5 black mx-8"></v-divider>
        <v-row align="center" justify="center">
          <v-col class="mt-1" align="center">
            <v-card-text
              class="font-weight-black overline"
              :color="this.status_lock == 'lock' ? 'red' : 'green'"
              >Status : {{ this.status_lock }}
            </v-card-text>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
  </div>
</template>

<script>
// import HelloWorld from '@/components/Dashboard_Device.vue'

export default {
  name: "Home",
  components: {},
  data: () => ({
    array_device: [],
    Devicename: "Device Name:",
    Deviceid: "ID:",
    loading: false,
    bluetoothDevice: null,
    statusCharacteristic: null,
    server: null,
    service: null,
    characteristic: null,
    otp_admin: "123454",
    name: "",
    id: "",
    status: "connect",
    status_icon: "mdi-bluetooth-off",
    status_icon_btn: "mdi-arrow-right-circle",
    status_lock: "lock",
    statuslock_icon: "mdi-lock-outline",
    devicegatt: null,
    backgroung_state_device:
      "background: linear-gradient(to right, #e52d27, #b31217);",
  }),
  mounted: {},
  created() {
    this.ScanDevice();
  },
  methods: {
    ScanDevice() {
      return (this.bluetoothDevice
        ? Promise.resolve()
        : this.requestDevice()
      ).catch((error) => {
        console.log("Argh! " + error);
      });
    },
    requestDevice() {
      console.log("Requesting any Bluetooth Device...");

      this.$message
        .loading("Scan Device...", 2.0)
        .then(() => this.$message.success("Scan Device", 1.5));

      return navigator.bluetooth
        .requestDevice({

  //         filters: [{
  //         services: ['1fb32be3-958f-463c-893e-f9e33911f256']
  // }]

         acceptAllDevices: true,
         optionalServices: ['1fb32be3-958f-463c-893e-f9e33911f256']
        })
        .then((device) => {
          this.bluetoothDevice = device;
          console.log(device.rssi);
          var array_data = {
            id: device.id,
            name: device.name,
          };

          this.array_device.push(array_data);
          console.log(this.array_device);
        });
    },

    connectDeviceAndCacheCharacteristics() {
      if (this.status_lock == "lock") {
        this.status_lock = "unlock";
        this.statuslock_icon = "mdi-lock-open-outline";

        if (this.bluetoothDevice.gatt.connected && this.statusCharacteristic) {
          return Promise.resolve();
        }
        return this.bluetoothDevice.gatt
          .connect()
          .then((server) => {
            console.log("server--->");
            return server.getPrimaryService(
              "1fb32be3-958f-463c-893e-f9e33911f256"
            );
          })
          .then((service) => {
            console.log("service--->");
            return service.getCharacteristic(
              "da467c88-9e6a-4d73-8f15-1dca6bc67419"
            );
          })
          .then((characteristic) => {
            console.log("characteristicr--->");
            const encoder = new TextEncoder("utf-8");
            const userDescription = encoder.encode(this.otp_admin);
            characteristic.writeValue(userDescription);

            this.$message
              .loading("Unlock Device...", 1.5)
              .then(() => this.$message.success("Unlock success", 1.5));

            this.Timeout_StatusLock_unlock();
          });
      }
    },
    Status_connect_dis_Device() {
      this.loading = true;
      // console.log(this.bluetoothDevice.gatt.connected);
      if (this.status == "connect") {
        this.bluetoothDevice.gatt.connect();
        this.devicegatt = this.bluetoothDevice.gatt.connect();

        this.$message
          .loading("Connect to Device...", 1.5)
          .then(() => this.$message.success("Connect Success", 2.0));

        this.status_icon_btn = "mdi-close-circle";
        this.status = "disconnect";
        this.status_icon = "mdi-bluetooth-connect";
        this.loading = false;
      } else if (this.status == "disconnect") {
        this.bluetoothDevice.gatt.disconnect();

        this.$message
          .loading("Disconnect to Device...", 1.5)
          .then(() => this.$message.success("Disconnect Success", 2.0));

        this.status_icon_btn = "mdi-arrow-right-circle";
        this.status = "connect";
        this.status_icon = "mdi-bluetooth-off";

        this.loading = false;
      }
    },
    Timeout_StatusLock_unlock() {
      setTimeout(
        () => (
          (this.status_lock = "lock"),
          (this.statuslock_icon = "mdi-lock-outline"),
          this.$message
            .loading("Action Lock..", 1.5)
            .then(() => this.$message.error("Lock success", 2.5))
        ),
        8000
      );
    },
  },
};
</script>
<style lang="scss" scoped>
.truncatecus {
  max-width: 220px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>